.modal-container  {
    display: flex;
    position: absolute;
    z-index: 10;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .5);
    justify-content: center;
    align-items: center;
}

.modal-body  {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    width: 600px;
    max-width: 90%;
    border-radius: 10px;
    padding: 20px;
}

.modal-header {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 0 20px;
}

.modal-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
}

.modal-buttons {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px
}