.menu {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    margin: 0 auto;
}

.menu h2{
    font-family: 'Rubik Dirt', cursive;
    font-size: calc(2em + 1vw) !important;
    padding-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.dark .menu h2 {
    color: #fff !important;
}